import PropTypes from "prop-types";
import React, { Fragment, useState } from "react";
import { Link } from "react-router-dom";
import MetaTags from "react-meta-tags";
import { connect } from "react-redux";
import { BreadcrumbsItem } from "react-breadcrumbs-dynamic";
import { getDiscountPrice } from "../../helpers/product";
import LayoutThree from "../../layouts/LayoutThree";
import Breadcrumb from "../../wrappers/breadcrumb/Breadcrumb";
import { useFormik } from "formik";
import * as Yup from "yup";

const Checkout = ({ location, cartItems, currency }) => {
  const { pathname } = location;
  let cartTotalPrice = 0;
  const [status, setStatus] = useState(false);
  const [status1, setStatus1] = useState(false);

  const formik = useFormik({
    initialValues: {
      fname: "",
      lname: "",
      email: "",
      phone: "",
      country: "",
      address: "",
      address1: "",
      city: "",
      state: "",
      zip: "",
      password: "",
      notes: "",
    },
    validationSchema: Yup.object({
      fname: Yup.string()
        .min(2, "Mininum 2 characters")
        .max(15, "Maximum 15 characters")
        .required("First name is a required field!"),

      lname: Yup.string()
        .min(2, "Mininum 2 characters")
        .max(15, "Maximum 15 characters")
        .required("Last name is a required field!"),

      email: Yup.string()
        .email("Invalid email format")
        .required("Email is a required field!"),

      phone: Yup.string().required("phone Number is a required field!"),

      zip: Yup.string().required("zip is a required field!"),

      password: Yup.string()
        .min(6, "Minimum 6 characters")
        .required("password is a required field!"),

      country: Yup.string().required("Country is a required field!"),
      city: Yup.string().required("city is a required field!"),
      state: Yup.string().required("state is a required field!"),

      address: Yup.string()
        .min(2, "Mininum 5 characters")
        .max(15, "Maximum 15 characters")
        .required("House name is a required field!"),

      address1: Yup.string()
        .min(2, "Mininum 5 characters")
        .max(15, "Maximum 15 characters")
        .required("Apartment is a required field!"),

      notes: Yup.string()
        .min(2, "Mininum 10 characters")
        .max(15, "Maximum 15 characters")
        .required("notes is a required field!"),
    }),
    onSubmit: (values, { resetForm }) => {
      alert(JSON.stringify(values, null, 2));
      resetForm({ values: "" });
    },
  });

  return (
    <Fragment>
      <MetaTags>
        <title>Impetrosys</title>
        <meta
          name="description"
          content="Checkout page of flone react minimalist eCommerce template."
        />
      </MetaTags>
      {/* <BreadcrumbsItem to={process.env.PUBLIC_URL + "/"}>Home</BreadcrumbsItem>
      <BreadcrumbsItem to={process.env.PUBLIC_URL + pathname}>
        Checkout
      </BreadcrumbsItem> */}
      <LayoutThree headerTop="visible">
        {/* breadcrumb */}
        {/* <Breadcrumb /> */}
        {/* <div className="contact-area-new">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
          <div className="custom-new">
            <h4>Your wishlist items</h4>
            </div>
            </div>
            </div>
            </div>
            </div> */}
        <div className="checkout-area pt-10 pb-30">
          <form onSubmit={formik.handleSubmit}>
            {/* <h3 className="cart-page-title">Your wishlist items</h3> */}
            <div className="container">
              <div className="billing-info-wrap-new">
                <h2> Your Checkout</h2>
              </div>
              {cartItems && cartItems.length >= 1 ? (
                <div className="row">
                  <div className="col-lg-7">
                    <div className="billing-info-wrap">
                      <div className="row">
                        <div className="col-lg-6 col-md-6">
                          <div className="billing-info mb-20">
                            <label>First Name</label>
                            <input
                              type="text"
                              name="fname"
                              value={formik.values.fname}
                              onChange={formik.handleChange}
                            />
                            {formik.errors.fname && formik.touched.fname && (
                              <p className="text-danger">
                                {formik.errors.fname}
                              </p>
                            )}
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6">
                          <div className="billing-info mb-20">
                            <label>Last Name</label>
                            <input
                              type="text"
                              name="lname"
                              value={formik.values.lname}
                              onChange={formik.handleChange}
                            />
                            {formik.errors.lname && formik.touched.lname && (
                              <p className="text-danger">
                                {formik.errors.lname}
                              </p>
                            )}
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6">
                          <div className="billing-info mb-20">
                            <label>Phone</label>
                            <input
                              name="phone"
                              placeholder="Mobile Number"
                              type="number"
                              value={formik.values.phone}
                              onChange={formik.handleChange}
                            />
                            {formik.errors.phone && formik.touched.phone && (
                              <p className="text-danger">
                                {formik.errors.phone}
                              </p>
                            )}
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6">
                          <div className="billing-info mb-20">
                            <label>Email Address</label>
                            <input
                              type="email"
                              name="email"
                              value={formik.values.email}
                              onChange={formik.handleChange}
                            />
                            {formik.errors.email && formik.touched.email && (
                              <p className="text-danger">
                                {formik.errors.email}
                              </p>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="billing-info-wrap">
                      <h3>Billing Details</h3>

                      <div className="row">
                        <div className="col-lg-12">
                          <div className="billing-select mb-20">
                            <label>Country</label>
                            <select
                              type="email"
                              name="country"
                              value={formik.values.country}
                              onChange={formik.handleChange}
                            >
                              <option>Select a country</option>
                              <option>Azerbaijan</option>
                              <option>Bahamas</option>
                              <option>Bahrain</option>
                              <option>Bangladesh</option>
                              <option>Barbados</option>
                            </select>
                            {formik.errors.country &&
                              formik.touched.country && (
                                <p className="text-danger">
                                  {formik.errors.country}
                                </p>
                              )}
                          </div>
                        </div>
                        <div className="col-lg-12">
                          <div className="billing-info mb-20">
                            <label>Street Address</label>
                            <input
                              className="billing-address"
                              placeholder="House number and street name"
                              type="text"
                              name="address"
                              value={formik.values.address}
                              onChange={formik.handleChange}
                            />
                            {formik.errors.address &&
                              formik.touched.address && (
                                <p className="text-danger">
                                  {formik.errors.address}
                                </p>
                              )}

                            <input
                              placeholder="Apartment, suite, unit etc."
                              type="text"
                              name="address1"
                              value={formik.values.address1}
                              onChange={formik.handleChange}
                            />
                            {formik.errors.address1 &&
                              formik.touched.address1 && (
                                <p className="text-danger">
                                  {formik.errors.address1}
                                </p>
                              )}
                          </div>
                        </div>
                        <div className="col-lg-12">
                          <div className="billing-info mb-20">
                            <label>Town / City</label>
                            <input
                              type="text"
                              name="city"
                              value={formik.values.city}
                              onChange={formik.handleChange}
                            />
                            {formik.errors.city && formik.touched.city && (
                              <p className="text-danger">
                                {formik.errors.city}
                              </p>
                            )}
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6">
                          <div className="billing-info mb-20">
                            <label>State</label>
                            <input
                              type="text"
                              name="state"
                              value={formik.values.state}
                              onChange={formik.handleChange}
                            />
                            {formik.errors.state && formik.touched.state && (
                              <p className="text-danger">
                                {formik.errors.state}
                              </p>
                            )}
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6">
                          <div className="billing-info mb-20">
                            <label>Postcode / ZIP</label>
                            <input
                              type="number"
                              name="zip"
                              value={formik.values.zip}
                              onChange={formik.handleChange}
                            />
                            {formik.errors.zip && formik.touched.zip && (
                              <p className="text-danger">{formik.errors.zip}</p>
                            )}
                          </div>
                        </div>
                      </div>
                      <div className="d-block">
                        <div className="custom-control custom-checkbox">
                          <input
                            id="status1"
                            name="paymencheckbox tMethod"
                            type="checkbox"
                            className="custom-control-input"
                            onClick={() => setStatus1(!status1)}
                            required
                          />
                          <label className="custom-control-label" for="status1">
                            Create an account?
                          </label>
                        </div>
                        {status1 ? (
                          <div className="row">
                            <div className="col-lg-6 col-md-6">
                              <p className="pt-2">
                                Create an account by entering the information
                                below. If you are a returning customer please
                                login at the top of the page.
                              </p>
                              <div className="billing-info mb-20">
                                <label>Account Password</label>
                                <input
                                  type="text"
                                  name="password"
                                  value={formik.values.password}
                                  onChange={formik.handleChange}
                                />
                                {formik.errors.password &&
                                  formik.touched.password && (
                                    <p className="text-danger">
                                      {formik.errors.password}
                                    </p>
                                  )}
                              </div>
                            </div>
                          </div>
                        ) : null}
                      </div>

                      <div className="d-block my-3">
                        <div className="custom-control custom-checkbox">
                          <input
                            id="credit"
                            name="paymencheckbox tMethod"
                            type="checkbox"
                            className="custom-control-input"
                            onClick={() => setStatus(!status)}
                            required
                          />
                          <label className="custom-control-label" for="credit">
                            Ship To A Different Address?
                          </label>
                        </div>
                        {status ? (
                          <div className="row">
                            <div className="col-lg-12">
                              <div className="billing-select mb-20 mt-2">
                                <label>Country</label>
                                <select
                                  type="email"
                                  name="country"
                                  value={formik.values.country}
                                  onChange={formik.handleChange}
                                >
                                  <option>Select a country</option>
                                  <option>Azerbaijan</option>
                                  <option>Bahamas</option>
                                  <option>Bahrain</option>
                                  <option>Bangladesh</option>
                                  <option>Barbados</option>
                                </select>
                                {formik.errors.country &&
                                  formik.touched.country && (
                                    <p className="text-danger">
                                      {formik.errors.country}
                                    </p>
                                  )}
                              </div>
                            </div>
                            <div className="col-lg-12">
                              <div className="billing-info mb-20">
                                <label>Street Address</label>
                                <input
                                  className="billing-address"
                                  placeholder="House number and street name"
                                  type="text"
                                  name="address"
                                  value={formik.values.address}
                                  onChange={formik.handleChange}
                                />
                                {formik.errors.address &&
                                  formik.touched.address && (
                                    <p className="text-danger">
                                      {formik.errors.address}
                                    </p>
                                  )}
                                <input
                                  placeholder="Apartment, suite, unit etc."
                                  type="text"
                                  name="address1"
                                  value={formik.values.address1}
                                  onChange={formik.handleChange}
                                />
                                {formik.errors.address1 &&
                                  formik.touched.address1 && (
                                    <p className="text-danger">
                                      {formik.errors.address1}
                                    </p>
                                  )}
                              </div>
                            </div>
                            <div className="col-lg-12">
                              <div className="billing-info mb-20">
                                <label>Town / City</label>
                                <input
                                  type="text"
                                  name="city"
                                  value={formik.values.city}
                                  onChange={formik.handleChange}
                                />
                                {formik.errors.city && formik.touched.city && (
                                  <p className="text-danger">
                                    {formik.errors.city}
                                  </p>
                                )}
                              </div>
                            </div>
                            <div className="col-lg-6 col-md-6">
                              <div className="billing-info mb-20">
                                <label>State</label>
                                <input
                                  type="text"
                                  name="state"
                                  value={formik.values.state}
                                  onChange={formik.handleChange}
                                />
                                {formik.errors.state &&
                                  formik.touched.state && (
                                    <p className="text-danger">
                                      {formik.errors.state}
                                    </p>
                                  )}
                              </div>
                            </div>
                            <div className="col-lg-6 col-md-6">
                              <div className="billing-info mb-20">
                                <label>Postcode / ZIP</label>
                                <input
                                  type="number"
                                  name="zip"
                                  value={formik.values.zip}
                                  onChange={formik.handleChange}
                                />
                                {formik.errors.zip && formik.touched.zip && (
                                  <p className="text-danger">
                                    {formik.errors.zip}
                                  </p>
                                )}
                              </div>
                            </div>
                            <div className="col-lg-6 col-md-6">
                              <div className="billing-info mb-20">
                                <label>Phone</label>
                                <input
                                  name="phone"
                                  placeholder="Mobile Number"
                                  type="number"
                                  value={formik.values.phone}
                                  onChange={formik.handleChange}
                                />
                                {formik.errors.phone &&
                                  formik.touched.phone && (
                                    <p className="text-danger">
                                      {formik.errors.phone}
                                    </p>
                                  )}
                              </div>
                            </div>
                          </div>
                        ) : null}
                      </div>
                      <div className="additional-info-wrap">
                        <h4>Additional information</h4>
                        <div className="additional-info">
                          <label>Order notes</label>
                          <textarea
                            placeholder="Notes about your order, e.g. special notes for delivery. "
                            name="notes"
                            defaultValue={""}
                            value={formik.values.notes}
                            onChange={formik.handleChange}
                          />
                          {formik.errors.notes && formik.touched.notes && (
                            <p className="text-danger">{formik.errors.notes}</p>
                          )}
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="col-lg-5">
                    <div className="your-order-area">
                      <h3>Your order</h3>
                      <div className="your-order-wrap gray-bg-4">
                        <div className="your-order-product-info">
                          <div className="your-order-top">
                            <ul>
                              <li>Product</li>
                              <li>Total</li>
                            </ul>
                          </div>
                          <div className="your-order-bottom">
                            <ul>
                              <li className="your-order-shipping-new">
                                Cocktail Yellow Dress
                              </li>
                              <li>₹450.00</li>
                            </ul>
                          </div>

                          <div className="your-order-bottom">
                            <ul>
                              <li className="your-order-shipping-new">
                                Order Tax
                              </li>
                              <li>0.00</li>
                            </ul>
                          </div>
                          <div className="your-order-bottom">
                            <ul>
                              <li className="your-order-shipping">Shipping</li>
                              <li>Free</li>
                            </ul>
                          </div>
                          <div className="your-order-total">
                            <ul>
                              <li className="order-total">Total</li>
                              <li>₹450.00</li>
                              {/* <li>
                              {currency.currencySymbol +
                                cartTotalPrice.toFixed(2)}
                            </li> */}
                            </ul>
                          </div>
                        </div>

                        {/* /// */}
                        <div className="d-block my-3">
                          <div className="custom-control custom-radio">
                            <input
                              id="credit3"
                              name="credit3"
                              type="radio"
                              className="custom-control-input"
                              checked
                            />
                            <label
                              className="custom-control-label"
                              for="credit3"
                            >
                              Cash on Delivery
                            </label>
                          </div>
                          <div className="custom-control custom-radio">
                            <input
                              id="credit2"
                              name="credit2"
                              type="radio"
                              className="custom-control-input"
                            />
                            <label
                              className="custom-control-label"
                              for="credit2"
                            >
                              Net Banking
                            </label>
                          </div>
                          <div className="custom-control custom-radio">
                            <input
                              id="debit"
                              name="debit"
                              type="radio"
                              className="custom-control-input"
                            />
                            <label className="custom-control-label" for="debit">
                              Debit card/Credit card
                            </label>
                          </div>
                          <div className="custom-control custom-radio">
                            <input
                              id="paypal"
                              name="paypal"
                              type="radio"
                              className="custom-control-input"
                            />
                            <label
                              className="custom-control-label"
                              for="paypal"
                            >
                              PayPal
                            </label>
                          </div>
                        </div>

                        <div className="place-order mt-25">
                          <button className="btn-hover">Place Order</button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ) : (
                <div className="row">
                  <div className="col-lg-12">
                    <div className="item-empty-area text-center">
                      <div className="item-empty-area__icon mb-30">
                        <i className="pe-7s-cash"></i>
                      </div>
                      <div className="item-empty-area__text">
                        No items found in cart to checkout <br />{" "}
                        <Link to={process.env.PUBLIC_URL + "/shop"}>
                          Shop Now
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </form>
        </div>
      </LayoutThree>
    </Fragment>
  );
};

Checkout.propTypes = {
  cartItems: PropTypes.array,
  currency: PropTypes.object,
  location: PropTypes.object,
};

const mapStateToProps = (state) => {
  return {
    cartItems: state.cartData,
    currency: state.currencyData,
  };
};

export default connect(mapStateToProps)(Checkout);
